import React from 'react';
import {connect} from 'react-redux';
import * as Actions from '../Actions';
import * as Status from '../Status';

class PayInfo extends React.Component {
    constructor(props) {
        super(props);
        this.props.onFresh();
    }


    // 
    render() {
        switch (this.props.status) {
            case Status.Success:
                return (
                    <div>
                        <div><h1></h1></div>
                        <table border={"1"}>
                            <tbody>
                            <tr>
                                <th></th>
                                <th>ID</th>
                                <th></th>
                            </tr>
                            {

                                this.props.list.map(function (item) {
                                    return (

                                        <tr>
                                            <td key={`${Date.now()}`}>{item.orderid}</td>
                                            <td>{item.orderuid}</td>
                                            <td>{item.price}</td>
                                        </tr>)
                                })

                            }
                            </tbody>
                        </table>
                        <button onClick={this.props.onFresh}></button>
                    </div>
                );
            case Status.Loading:
                return (
                    <div>
                        ...
                        <button onClick={this.props.onFresh}></button>
                    </div>
                );
            default:
                throw new Error('unexcepted status ' + this.props.status);
        }
    }
}

// propsdispatch
const mapDispatchToProps = (dispatch) => {
    return {
        onFresh: () => {
            dispatch(Actions.fresh());
        }
    }
};

// propsstate
const mapStateToProps = (state) => {
    state = state.pay;
    return {
        status: state.status,
        list: state.list
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(PayInfo);