import axios from 'axios';

export const fetchList = () => {
    return new Promise(function (resolve, reject) {
        axios.post('/SSMP/fetchPayList'
        ).then(function (res) {
            //  
            console.log(res);
            if(res.status===200)
                resolve(res.data.data);
            reject();
        }).catch(function (error) {
            // 
            console.log(error);
            reject();
        });
    });
};