import 'react-quill/dist/quill.snow.css';
import React from 'react';
import {connect} from 'react-redux';
import * as Actions from '../Actions';
import Editor from 'react-umeditor';
import  * as EditorProps from '../EditorProps';

class Input extends React.Component {
    constructor(props){
        super(props);
        this.state = { // 
            content:""
        };
        this.handleFormChange = this.handleFormChange.bind(this);
        this.handleSubmitForm = this.handleSubmitForm.bind(this);
    }
    // 
    handleFormChange(content){
        this.props.onTextChange(this.refs.editor.getContent());
    }
    // 
    handleSubmitForm(){

    }
    // 
    render() {
        // Icons
        let icons = EditorProps.Icons;

        // 
        let plugins = {
            image:{
                uploader:EditorProps.DefaultUploader
            }
        };

        return (
            <div>
            <Editor ref="editor"
                    icons={icons}
                    plugins={plugins}
                    value={this.state.content}
                    onChange={this.handleFormChange}/>
            <input type="submit" value="" onClick={this.handleSubmitForm} />
        </div>
        )
    }
}

// propsdispatch
const mapDispatchToProps = (dispatch) => {
    return {
        onTextChange: (content) => {
            dispatch(Actions.inputUpdating(content));
        }
    }
};


export default connect(null, mapDispatchToProps)(Input);