import axios from 'axios';

export const uploadContent = (content) => {
    return new Promise(function (resolve, reject) {
        axios.post('/uploadImg', {
            content: content,
        }).then(function (res) {
            //  
            resolve(res);
        }).catch(function (error) {
            // 
            console.log(error);
            reject();
        });
    });
};